<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
import { getContractList } from '@/api'
export default {
  name: 'app',
  mounted () {
    this.getWalletInfo()
    this.getContractList()
  },
  methods: {
    ...mapMutations({
      setAddress: 'SET_ADDRESS',
      setContractList: 'SET_CONTRACTLIST'
    }),
    getWalletInfo () {
      const self = this
      window.postMessage({
        target: 'contentscript',
        data: {},
        method: 'getAccount'
      }, '*')
      window.addEventListener('message', function (e) {
        if (e.data && e.data.data && e.data.data.account) {
          self.setAddress(e.data.data.account)
          console.log('获取钱包地址成功')
        }
      })
    },
    getContractList () {
      getContractList().then(res => {
        console.log('合同列表：', res)
        console.table(res)
        try {
          this.setContractList(res)
        } catch (e) {
          console.log(e)
          this.$message.error('合同信息加载失败，请稍后再试！')
        }
      })
    }
  }
}
</script>

<style lang="less">
@import "./assets/style/index.less";
#app {
  background-color: #ebeff2;
  min-height: 100vh;
  max-width: 100vw;
  font-size: 16px;
  width: 100vw;
  overflow: hidden;
}
</style>



