import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/home',
      component: () => import('@/pages/home')
    },
    {
      // test
      path: '/',
      component: () => import('@/pages/index'),
      children: [
        {
          path: '/',
          redirect: '/home'
        },
        {
          path: '/my',
          component: () => import('@/pages/list/index'),
          children: [
            {
              path: '/',
              component: () => import('@/pages/list/list')
            },
            {
              path: 'detail/:id',
              component: () => import('@/pages/addAndModify')
            }
          ]
        },
        {
          path: '/recive',
          component: () => import('@/pages/list/index'),
          children: [
            {
              path: '/',
              component: () => import('@/pages/list/list')
            },
            {
              path: 'detail/:id',
              component: () => import('@/pages/addAndModify')
            }
          ]
        },
        {
          path: '/draft',
          component: () => import('@/pages/list/index'),
          children: [
            {
              path: '/',
              component: () => import('@/pages/list/list')
            },
            {
              path: 'detail/:id',
              component: () => import('@/pages/addAndModify')
            }
          ]
        },
        {
          path: '/add',
          component: () => import('@/pages/addAndModify')
        },
        {
          path: '/help',
          component: () => import('@/pages/help')
        }
      ]
    },
    {
      path: '*',
      component: () => import('@/pages/error')
    }
  ]
})

export default router



