import * as types from './mutation-types'

const matutaions = {
  [types.SET_ADDRESS] (state, address) {
    state.address = address
  },
  [types.SET_CONTRACTLIST] (state, contractList) {
    state.contractList = contractList
  }
}

export default matutaions



