import config from '@/base/config'
// var NebPay = require('nebpay')
import NebPay from 'nebpay'
const nebPay = new NebPay()

export function requestData (type, method, params = [], gas = 0) {
  return new Promise((resolve, reject) => {
    nebPay[type](config.dappAddress, gas, method, JSON.stringify([...params]), {
      listener: function (res) {
        if (res.result === '' && res.execute_err === 'contract check failed') {
          alert('合约检测失败，请检查浏览器钱包插件环境！')
          reject(JSON.parse(res.result))
        } else if (res === 'Network Error') {
          alert('网络错误，请稍后再试！')
        } else if (res.txhash) {
          resolve({code: 0, msg: '交易成功！'})
        } else {
          resolve(JSON.parse(res.result))
        }
      }
    })
  })
}



